import { Button, Stack } from '@mui/material';
import React from 'react';
import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { useDataStore } from '../store/store';

function Navigation() {
    const name = ["", "", "", "", "", ""]
    const { categori, categoriId, setCategori } = useDataStore();
    const history = useNavigate();
    const [selected, setSelected] = useState(0);
    function categoris() {
        const arr = []
        for (let i = 0; i < categori.length; i++) {
            arr.push(
                <Link to={`/${categori[i]}`} replace>
                    <Button variant={selected === i ? 'contained' : 'outlined'} onClick={() => {
                        setSelected(i)
                        setCategori(categoriId[i])
                    }}>
                        {name[i]}
                    </Button>
                </Link>
            )
        }
        return arr
    }
    return (
        <Stack spacing={1} direction="row" justifyContent={'center'}>
            {categoris()}
        </Stack>
    )
}
function Nav() {
    return (
        <nav className="nav-Container">
            <Navigation className="nav-item"></Navigation>
        </nav>
    )
}

export default Nav;